<template>
<view class="" style="display: flex;flex-flow: column nowrap;align-items: center;width: 750upx; ">
	<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>

	<movable-area class="jichukuangjia" :style="'height:'+height+'px'  " v-if="current === 0">
		<!-- scale-area -->
		<movable-view class="beijingqiang" :x="x" :y="y" direction="all" @change="onChange" scale scale-min="0.5" scale-max="1">
			<view class="border-style" style="flex-flow: column nowrap; width: 50upx; overflow: hidden; padding-top:50upx ;">
				<view class="border-style" v-for="(item2,index2) in  Y_axis" :key="index2" style="width: 50upx;height: 250upx;font-size: 30upx;justify-content: center;align-items: center;：writing-mode:lr-tb">{{ Y_axis[index2]}}</view>

			</view>

			<view class="border-style" v-for="(item1,index1) in X_axis" :key="index1" style="flex-flow: column nowrap; width: 810upx; overflow: hidden;">
				<view class="" style="width: 100%;height: 50upx;font-size: 40upx;justify-content: center;align-items: center;">{{X_axis[index1]}}万+</view>
				<view class="border-style" v-for="(item2,index2) in  Y_axis" :key="index2" style="flex-flow: row wrap; width: 810upx;height: 230upx; overflow: hidden; padding: 12upx 12upx 12upx 12upx;">
					<view class="" v-for="(item3,index3) in item_card_list_deal[index1*X_axis.length+index2]" :key="index3">
						<view class="card_view1" @click="tap_item(item3)" :style="'background-image:url('+item3.app_pic_url+');'">
						</view>
					</view>
					<view class="icontext card_view1" style="font-size: 50upx;color: Gold; font-weight: bold;" @click="gotonewitem">&#xe641;</view>
				</view>
			</view>
		</movable-view>
	</movable-area>


	<scroll-view scroll-y class="jichukuangjia"  :style="'height:'+height+'px' " v-if="current === 1">
				<handleview  :card_id="luyouData[list_id].card_id"></handleview>
	</scroll-view>


</view>
</template>

<script>
import navigationview from '../../../components/navigation.vue';
import handleview      from '../../../components/handle-view.vue';

// import luyouData from '../../common/luyou.js'
export default {
	data() {
		return {
			luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
			],
			list_id: null,
			items: [
				'版图',
				'改进',
			],
			height: 0,
			current: 0,
			item_card_list: [],
			item_card_list_deal: [],
			Y_axis: [],
			X_axis: [],
		}
	},
	components: {
		handleview,
		navigationview,
	},
	onLoad: function(option) {
		console.log("list_id:" + option.list_id)
		this.list_id = option.list_id;
		this.height = uni.getSystemInfoSync().windowHeight - 71 * (uni.getSystemInfoSync().windowWidth / 750);
		setTimeout(() => this.deal_with_data(), 500);
	},

	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},

		onChange: function(e) {
			//this.old.x = e.detail.x
			//	this.old.y = e.detail.y
			// console.log(e.detail.x, e.detail.y)
		},
		


		gotonewitem: function() {
			uni.navigateTo({
				url: "../item_detail/item_detail?data={}&card_id=" + this.luyouData[this.list_id].card_id +
					"&new_item_flag=true",
			})
		},
		
		deal_with_data: async function() {

			const that = this;
			let getdataresult0 = await that.get_hot_card_list();
			let getdataresult2 = await that.getData_list();
			this.Y_axis = that.luyouData[this.list_id].Y_axis.split(',')
			this.X_axis = that.luyouData[this.list_id].X_axis.split(',')
			
			for (var j = 0; j < this.X_axis.length; j++) {
				for (var i = 0; i < this.Y_axis.length; i++) {
					var num = 0;
					var mid1 = [];
					for (var k = 0; k < this.item_card_list.length; k++) {


						if (this.item_card_list[k][this.luyouData[this.list_id].X_axis_name] == this.X_axis[j] && this.item_card_list[k]
							[this.luyouData[this.list_id].Y_axis_name] == this.Y_axis[i]) {
							mid1[num] = this.item_card_list[k];
							num += 1;
						}
					}
					this.item_card_list_deal.push(mid1);

				}
			}
			// console.log(this.item_card_list_deal)
			// console.log(this.item_card_list_deal)
			// console.log(mid1)
			// console.log(mid2)
		},
	
		getData_list() {
			return new Promise((res) => {
				// return new Promise((res, rej) => {
				uni.request({
					url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
					method: "POST",
					data: {
						'form': this.luyouData[this.list_id].getdata_title,
						'data1': '499',
						'data2': '',
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					fail: (e) => {
						res(e);
					},
					success: (ret) => {
						if (ret.statusCode !== 200) {
							console.log("请求失败:", ret)
						} else {
							console.log("下载数据:", ret.data)
							// let lists = [];
							this.item_card_list = ret.data.data;
							// console.log("lists:", ret.data.data.length)
							// console.log("lists:", ret.data.data)
						}
						res(ret);
					}
				});
			})
		},
	
		tap_item: function(item3) {

			uni.navigateTo({
				url: "../item_detail/item_detail?data=" + JSON.stringify(item3) + "&card_id=" + this.luyouData[this.list_id].card_id,
			})
		}

	}
}
</script>

<style>
.jichukuangjia {
	display: flex;
	/*   position: fixed;
	bottom: 0upx; */
	/* 		background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/bc.jpg'); */
	background-color: white;
	width: 750upx;
	overflow: hidden;
}

.beijingqiang {
	/* background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/timg.jpg'); */
	display: flex;
	background-color: white;
	width: 5000upx;
	height: 6400upx;
	padding: 400upx 400upx 400upx 400upx;
	flex-flow: row nowrap;
}


.card_view0 {
	width: 50upx;
	height: 50upx;
	margin: 10upx 10upx 10upx 10upx;

}

.card_view1 {
	margin: 6upx 6upx 6upx 6upx;
	width: 48upx;
	height: 48upx;
	background-size: 100%100%;
	/* margin: 800upx 0upx 0upx 800upx; */
}
</style>



